@tailwind base;
@tailwind components;
@tailwind utilities;

.safariTransformFix {
  -webkit-transform: translate3d(0, 0, 0);
}

.fillAvailable {
  width: stretch;
}

@layer components {
  .layout {
    @apply py-6 px-12 phone:p-3 fillAvailable safariTransformFix mx-auto max-w-[1000px];
  }
}

html,
body {
  margin: 0px;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}
